<template>
  <div class="login" @keydown.enter="login">
    <van-cell-group>
        <van-field v-model="username" label="账号" placeholder="请输入用户名" class="oInput" />
        <van-field v-model="password" label="密码" placeholder="请输入密码" class="oInput" />
    </van-cell-group>
    <van-button @click="login" type="primary">主要按钮</van-button>
  </div>
</template>

<script>
import {instance2} from '@/utils/http';
import Vue from 'vue';
import { Field,Cell,Button,CellGroup } from 'vant';

Vue.use(CellGroup);
Vue.use(Cell);
Vue.use(Button);
Vue.use(Field);
export default {
    data(){
        return{
            username:'',
            password:''
        }
    },
    methods:{
        login(){
            if(!(this.username || this.password)){
                alert('请输入账号和密码')
                return false
            }
            instance2.post('/api/user/loginin',{
                username:this.username,
                password:this.password
            }).then(res=>{
                localStorage.setItem('token',res.token)
                this.$router.push('/center')
            }).catch(err=>{
                alert('账号或密码不正确，请重新输入')
                this.$router.push('/login')
                this.password = ''
                this.username = ''
            })
        }
    }
}

</script>
<style lang="scss" scoped>
.login{
    position: fixed;
    top: 25%;
    width: 100%;
    .oInput{
        margin-bottom: 5px;
    }
    button{
        width: 100%;
    }
}
</style>